  section.tos
    h1 利用規約

    p この利用規約（以下「本規約」といいます）は、株式会社ITヒーロー（以下「事業者」といいます）が提供するサブスプリクション型WEB制作サービス「tomarigi」（以下「本サービス」といいます。）の利用に関する条件を、本サービスを利用するお客様（以下「利用者」といいます）と事業者との間で定めるものです。利用者は本サービスを利用することにより、本規約の全ての記載内容について同意したものとみなします。本規約の規定とその他の利用条件に齟齬が生じた場合は、本規約を優先するものとします。

    section
      h2 第１条　規約の適用

      ol
        li 本規約は、本サービスの利用に係わる一切の関係について定めることを目的とし、本サービス及びコンテンツ、システムの利用に関するすべての事項に適用されます。
        li 事業者は、将来、本サービスの内容について、追加、変更又は削除することがあり、サービス内容の追加、変更又は削除に伴い、本規約の追加、変更、削除を行うことがあります。
        li 事業者は、本利用者の承諾を得ることなく、利用者に通知することなく、本規約を追加、変更又は削除することが出来るものとします。
        li 本規約の変更後、利用者が本サービスを利用した場合又は、１ヶ月内に本サービスの利用の終了手続きをとらなかった場合には、利用者は本規約の変更に同意したものとみなします。

    section
      h2 第２条　規約への同意

      ol
        li 本サービスをご利用になる前に、本規約を良くお読みください。
        li 利用者は、本規約の定めにしたがって本サービスを利用しなければなりません。
        li 利用者が本規約に同意されない場合、本サービスを利用することはできないものとします。
        li 本サービスを利用するにあたり、本規約に同意した上で利用申込をし、事業者がこれを承諾することによって、本規約を内容とする本サービスの利用契約（以下「本契約」といいます）が申込者と事業者との間で締結されます。
        li 事業者は、利用申込に以下の事由があると判断した場合、承諾しないことがあり、理由については一切の開示義務を負わないものとします。

        ul
          li 利用申込に際して、内容の全部又は１部につき偽造、誤記、記入漏れがあった場合
          li 過去に本規約違反などにより本契約の抹消処分を受けていることが判明した場合
          li その他、事業者が申込者及び申込の内容を適当でないと判断した場合

    section
      h2 第３条　届け出事項の変更

      ol
        li 申込者は、本サービスに係る契約申し込みの際、またはその後当社に届け出た内容に変更が生じた場合は速やかにその旨を当社に届け出るものとする。
        li 事業者は、申込者の登録情報が不正確もしくは不十分であった場合、または必要書類の提出をしなかった場合など、申込者の責に帰すべき事由に基づき登録情報を変更出来なかった場合、これに基づき本サービスを提供できず発生した損害に対して、一切その責任を負わない。
        li 当社は、登録されている連絡先が有効なものでない場合、または契約者が連絡先情報の提供を拒んでいる場合、本サービスを提供できず、その不達に起因して発生した損害に対して、一切その責任を負わない。

    section
      h2 第４条　本サービスの内容

      ol
        li 本サービスは利用者に対してウェブサイトの制作及び、ウェブサイト管理・保守・更新等のサポートをしていくサービスです。
        li 本サービスの営業時間は 、10:00～18:00 までとする。ただし、電話での受付は 16:00 までとす る。
        li  本サービスは、土曜、日曜、祝日は休業日とする。
        li 本サービスは、契約 1 口につき制作は 1 件とする。
        li 本サービスにおいて、更新のご依頼回数の上限は月４回までとし（修正依頼等も回数に含まれます。）、依頼回数を上限まで全て利用せず、未利用分の依頼回数が存在していても、当該月の末日をもって未利用分の依頼回数を利用する権利は消滅し、翌期間に繰り越すことはできないものとします。
        li 本サービスで依頼できる回数・内容・納期等は、必ずしも希望に添えるとは限らず、やむを得 ない場合はお断りする場合があります。
        li 利用者は事業者に対して、業務遂行の日時及び場所について指定又は管理することによって拘束したり、業務内容及び遂行方法について業務委託に必要な限界を超えた依頼をしてはなりません。また、業務上の規律に関する事項は事業者が決めるものとし、利用者が取り決めることはできないものとします。
        li 事業者は、利用者から与えられた依頼に対して業務を遂行致しますが、高度な専門知識に基づくアドバイス等の提供は目的としておりません。このため、法令に反するようなサービスの提供は本サービスの範囲を超えるものであり、利用者は、事業者に求める業務はいかなる場合であっても法令に反しない範囲でなければなりません。
        li 本サービスを提供する事業者は、利用者に対してはパートナー関係にあるものという意識をもち、提案を含め対応いたします。
        li 本サービスの内容、オプション等は、本サイト記載事項に準ずる。
        li 利用者の内部報告として、利用報告書等の作成は本サービスの範囲には含めないものとする。

    section
      h2 第５条　利用者の責任及び注意義務

      ol
        li 事業者が本サービスを遂行するに際して、利用者は必要な協力を行う。
        li 事業者に対し、損害を強要すると捉えられる行為、マルチビジネスに加担させる行為、利用者側の責任において起こった過誤に対して事業者に責任を求める行為など、サービスの提供を阻害する行為は禁止とします。これらに反する行為が見受けられた場合は、契約期間の途中であっても、事前に通知又は催告することなく、本サービスの利用契約を解除、又は本サービスの利用を一時的に停止いたします。
        li 事業者に提供する情報やテキスト原稿及び画像等のデータが、他人の名誉、社会的信用、プライバシー、肖像権、著作権その他の知的財産権、などの権利を侵害するものでないことを保証する。
        li 利用者は事業者に対し、他者の著作物を使用したことなどが原因で紛争、損害賠償の請求などが起こった場合の損害、責任について一切を免責するものとし、自らの責任をもって紛争に対処するものとします。
        li 事業者は、以下のいずれかに該当する場合には、利用者に事前に通知することなく、本サービスの利用の全部又は一部を停止又は中断することができるものとします。
        ul
          li 本サービスに係るコンピューター・システムの点検又は保守作業を緊急に行う場合
          li コンピューター、通信回線等が事故により停止した場合
          li 火災、停電、天災地変などの不可抗力により本サービスの運営ができなくなった場合
          li 決済等、サービスの重要な機能に障害が発生した場合
          li その他、事業者が停止又は中断を必要と判断した場合

    section
      h2 第６条　契約

      p 本契約の有効期間は、本契約締結の日から満１ヶ月とする。但し契約更新１ヶ月前までに利用者より解約の意思表示が無い場合、同一条件で契約更新するものとし、以後も同様とする。

      h2 第７条　サービス料金・支払

      ol
        li 利用者は、本サービスの利用対価として、事業者が定める利用料金（以下「サービス利用料」という）を、事業者が指定する方法により事業者に対して支払うものとします。なお、サービス利用料は、原則として、本サービスの利用開始時から予め定められた期間ごとにサービス利用料が発生し、当該期間にいかなる未利用分のサービス及び未利用分の日数があっても、減額されるものではありません。
        li 利用者は、本サービスを利用する上で追加的に生じる支出又は費用については、事業者の故意に基づき生じた利用者の損失となるものを除き、いかなる内容及び金額であっても、利用者が負担するものとし、このうち、事業者が立替えて負担した金額（以下「立替経費」という。）についても、事業者が指定する方法により事業者に対して支払うものとします。
        li 本契約により、事業者が利用者に提供する役務の内容は、一定の仕事の完成を目的とした請負業務ではないため、利用者が依頼した業務が完成に至らない場合であったとしても、サービス利用料は生じるものとします。
        li 利用者が初回サービス利用料をお支払い後、事業者は制作物を公開するものとします。なお公開後、制作物に掲載された内容に関しては、事業者は一切の責任を負いません。
        li 本サービスは、初回サービス利用料をお支払いされた時点から開始されます。利用者は事業者に対し、お客様の支払情報を保存する権限、及び予め定められた期間ごとに事業者指定のお支払い方法で請求する権限を付与します。事業者は、利用者が解約しない限り毎月、予め事業者が定めた日に、その時点でのサービス利用料に所定の税金を加算した金額を請求します。
        li 契約会社が本条の支払いを遅延した場合、契約会社は年７％の割合による遅延損害金を当社に支払うものとします。

    section
      h2 第８条　サービス利用期間と本契約の解約

      ol
        li 本サービスの利用は最低利用期間は１ヶ月とし、日割り計算はいたしません。
        li 本契約の解約及び本サービスの停止は、利用者が事業者への解約申告を行なった日から最短で満１ヶ月後となります。解約日及びサービス停止日による日割りのサービス利用料の返還はいたしません。また、本契約を解約しても未払いのサービス利用料を支払う義務が解消されることはありません。
        li 利用者が本契約の解約を行なった場合、利用者が本サービスを利用する権利は、解約時に即時喪失するものとします。

    section
      h2 第９条　サービスの停止

      ol
        li 前項に基づきサービスを停止した場合でも、サービス利用料の最終支払いの義務は残るものとし、また既に支払われたサービス料金の返還はいたしません。
        li 事業者が指定した支払い方法でお支払いが完了しなかった場合、事業者は利用者のサービスを一時停止することができます。
        li 利用者へのサービス停止に伴い、事業者は制作物の公開を停止するものとします。
        li 本条に基づきサービスを停止したことによって利用者に損害が発生した場合、事業者は一切その責任を負いません。
        li 事業者が提供する本サービスによる制作物は、事業者が提供する状態でのみ利用するものとし、制作物データの提供は原則しないものとする。

    section
      h2 第１０条　サービスの再開

      p サービス利用料の支払いの遅延などによってサービスが停止された後、本サービスの利用を再開する場合には、未払い分のサービス利用料の支払いが必要となります。

    section
      h2 第１１条　サービス内容などの変更と終了

      ol
        li 事業者は、本サービスの健全な遂行・運用に必要かつ合理的と判断した場合には、利用者への事前通知をもってサービスの内容及び料金を変更できるものとします。
        li 事業者は、事業者の都合により、本サービスを６ヶ月前の利用者への事前通知をもってサービスを終了できるものとします。
        li 事業者は、本条に基づき事業者が行なった措置に基づき利用者に生じた損害について一切の責任を負いません。

    section
      h2 第１２条　制作物の再制作

      ol
        li 制作物の大幅な再制作の必要がある場合は、費用は利用者が負担し、事業者が合理的な根拠に基づいて計算した追加料金を支払うものとします。
        li 利用者が事業者に提示した情報または指示の誤りに起因して再制作を行うこととなった場合には、事業者が合理的な根拠に基づいて計算した追加料金を支払うものとします。

    section
      h2 第１３条　制作物に対する責任

      ol
        li 本サービス利用中に、本制作物に隠れた瑕疵が発見された場合、事業者は速やかに利用者と協議し、必要な無償修補、対価の減額等を含む合理的措置を取り決めるものとします。但し当該瑕疵の原因が、本制作物に対して事業者以外の者による造作・工作がなされたことによる場合にはこの限りではありません。
        li 制作物公開後に利用者が成果物の改変、修正、設定変更、または、アップデート等を行った場合は、弊所はいっさいその責任を負わないものとします。
        li インターネット・Webサービスの技術の進歩により、世界的に使用される標準リソースも日々変化しています。事業者が責任を負うのは、制作物公開初日時点でのインターネット環境によるものとします。

    section
      h2 第１４条　禁止事項

      p 契約会社は、本サービスの利用に関して、以下に定める行為を行なってはなりません。

      ol(type="a")
        li 事業者に、長時間の架電・繰り返しの問い合わせを過度に行い、義務や理由のないことを強要し、事業者の業務に著しく支障を来たす行為。
        li 事業者に対し、弁護士法その他法令で認められない業務を行わせる行為。
        li 事業者への嫌がらせや、不良行為など業務の進行を妨げる行為。
        li 事業者の一般的に開示していない機密情報を詮索する行為。
        li 事業者に対して宗教、政治結社、マルチ商法等の勧誘をする行為。
        li 事業者への暴言・脅迫行為、又は事業者の業務の遂行を妨げる行為。その他事業者が利用者として不適当と判断した行為。
        li 事業者若しくは第三者の知的財産権その他の権利若しくは利益を侵害する行為、又はそのおそれのある行為。
        li 事業者若しくは第三者の名誉若しくは信用を毀損する行為、又はそのおそれのある行為。
        li 公序良俗に反する行為、又はそのおそれのある行為。
        li 犯罪的行為、犯罪的行為に結びつく行為若しくは法令に違反する行為、又はそのおそれのある行為。
        li 本契約に基づく権利又は義務の全部又は一部を第三者に譲渡し、継承させ、又は担保に共する行為。
        li 本サービスを利用する権利の全部又は一部を第三者に再許諾し、又は本サービスを第三者に利用させる行為。
        li その他事業者が不適当と判断する行為。

    section
      h2 第１５条　免責事項

      ol
        li 事業者は、本サービスを利用したこと、または利用できなかったこと等に起因する利用者の損害について、直接的又は間接的な損害を問わず一切責任を負わないものとします。
        li 本規約の条項のいずれかに利用者が違反した場合、利用者は事業者に対しての主張、訴訟その他全ての法的処置から当社を免責するものとします。
        li 事業者は、本サービスが利用者の特定の目的に適合すること、期待する機能・商品的価値・正確性・有用性・完全性を有すること、本サービスの利用が利用者に適用のある法令又は業界団体の内部規則等に適合すること、及び不都合が生じないことについて、何ら保証するものではありません。
        li 事業者は、事業者による本サービスの中断、停止、終了、利用不能又は、利用者が送信したメッセージ又は情報の削除又は消失、利用者の契約の抹消、本サービスの利用による契約情報の消失、又は機器の故障もしくは損傷、その他本サービス利用に関して契約会社が被った被害につき、賠償する責任を一切負わないものとします。
        li 事業者は、利用者の要請に応じ、業務遂行にあたり利用者の負担において支出又は費用が生じることがありますが、これら支出又は費用は、事業者の故意によるものを除き、全て利用者の負担とし、事業者は利用者及び第三者に対しても、これに関する求償、賠償等の請求は一切負わないものとします。
        li 事業者は、利用者と第三者との間において生じた取引、連絡、紛争等について、事業者の故意によるものを除き、一切責任を負いません。

    section
      h2 第１６条　不可抗力

      ol
        li 地震、台風、津波その他の天災地変、輸送機関の事故、不慮の事故や疾病その他の不可抗力により、本契約の全部又は一部の履行の遅延又は履行不能が生じた場合には、利用者及び事業者はその責任は負わないものとする。
        li 前項に定める事由が生じた場合には、直ちに相手方に対しその旨の通知をし、以後の対応について協議する。

    section
      h2 第１７条　秘密保持

      p 利用者は、本契約に基づいて事業者から開示され、又は本業務の遂行過程で取得した事業者の業務上、技術上、その他一切の情報（個人情報を含む。）については秘密情報として扱うものとし、事業者の事前の書面による承諾なく、これらの情報を公表若しくは第三者へ開示してはならない。

    section
      h2 第１８条　個人情報

      ol
        li 利用者が本サービスの利用を停止した場合、又は事業者が利用者の本サービスの利用を停止した場合であっても、事業者は、当該利用者から申し出があるまで当該利用者に関する情報を保持します。
        li 事業者による利用者の利用者情報の取り扱いについては、別途事業者のプライバシーポリシーの定めによるものとし、利用者は当該プライバシーポリシーに従って事業者が利用者の情報を取り扱うことについて同意するものとします。
        li 事業者は、利用者が事業者に提供した情報及びデータ等を、個人を特定できない形での統計的な情報として、事業者の裁量で、利用及び公開することができるものとし、利用者はこれに異議を唱えないものとします。

    section
      h2 第１９条　損害賠償

      p 利用者及び事業者は、本契約の履行に関し、相手方の責めに帰すべき事由により直接且つ現実に被った通常の損害に限り、相手方に対して損害賠償を訴求することができる、但し損害賠償額については、事業者が本業務の対価として定めたサービス利用料の６ヶ月分を累積限度額とする。

    section
      h2 第２０条　著作権

      p 利用者が、本制作物の使用に関して、第三者から権利侵害等の理由に基づく苦情又は請求を受けた場合は、利用者は事業者に対し遅延なくその旨を通知し、利用者と事業者は協議により必要且つ可能な対策を講ずるものとする。但し、利用者と第三者の紛争の原因が、制作物作成過程において利用者の指示、仕様に起因する場合は、事業者は責任を負わない。

    section
      h2 第２１条　権利帰属

